import React from 'react'
import Link from 'gatsby-link'
import Helmet from "react-helmet";
import Github from './github-56.svg'
import Mail from './mail-56.svg'
import Twitter from './twitter-56.svg'

const IndexPage = ({ data }) => (
  <div className="container card">
  <Helmet title={ 'Home - ' + data.site.siteMetadata.title } />
  <div className="row">
    <div className="col-12">
    <h1 className="center">Hi! I'm Ulises Avila and I welcome you to this blog</h1>
    <p>I am a Web Developer currently living in Mexico, and my favorite technologies are Python and JavaScript, so expect all programming related content to be about them.</p>
    <p>Far from being a tool for pushing my personal branding, this page is a journal for whatever I find interesting in my learning experience. So you can say that I am a jack of all trades but a master of none.</p>

    {/*Here my personal photo lies*/}

    <p>Then I welcome you again and wish that your time here is nice.</p>
    </div>
  </div>
  <hr className="separator"/>
  <div className="row">
    <div className="col-12">
    <h2 className="center">My latest posts</h2>
    <p>To check everything that my site can offer to you refer to the <Link to="/journal/">Journal</Link> section.</p>
    </div>
    <div className="col-12">
    <ol className="post-list">
      {data.allMarkdownRemark.edges.map(({ node }) =>
        <li key={node.id}>
          <Link
            to={"/journal/"+ node.frontmatter.category +node.frontmatter.path}
          >
            <span>
              <b>{node.frontmatter.title}{" "}
              <span color="#BBB">â€” {node.frontmatter.date}</span></b>
            </span>
            <div>
              {node.frontmatter.foreword} 
            </div>
          </Link>
        </li>
      )}
</ol>
    </div>
  </div>
  <hr className="separator"/>

  <div className="row">
    <div className="col-12">
    <h2 className="center">Contact me</h2>
    <p>If you want to keep in touch with me use any of these communication channels:</p>
    </div>

    <div className="col-12 center">
      <a href="https://github.com/UlisesAC4" rel="me"><img src={Github}></img></a>
      <a href="mailto:info@ulisesavila.com" rel="me"><img src={Mail}></img></a>
      <a href="https://twitter.com/UlisesAC4" rel="me"><img src={Twitter}></img></a>
    </div>
  </div>
    
  </div>
)

export default IndexPage

export const query = graphql
`query IndexBlogPostsQuery {
  site {
    siteMetadata {
      title
    }
  }
        allMarkdownRemark(sort: {order: DESC, fields: [frontmatter___date]} limit: 3) {
          edges {
            node {
              id
              timeToRead
              frontmatter {
                title
                date(formatString: "MMMM DD, YYYY")
                path
                category
                foreword
              }
              excerpt
            }
          }
        }
}`


